import Vue from 'vue'
import Router from 'vue-router'
/* web模块 */
import webMain from '@/view/web/main'
import webIndex from '@/view/web/index'
import webDetails from '@/view/web/details'
/* wap模块 */
import wapMain from '@/view/wap/main'
import wapIndex from '@/view/wap/index'
import wapDetails from '@/view/wap/details'
import wapKnowledge from '@/view/wap/knowledge'
/* 后台模块 */
import adminLogin from '@/view/admin/login'
import adminMain from '@/view/admin/main'
import adminIndex from '@/view/admin/index'
import adminUser from '@/view/admin/user'
import adminDream from '@/view/admin/dream'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'web',
      component: webMain,
      children: [
        {
          path: '/',
          component: webIndex
        },
        {
          path: ':id/details',
          component: webDetails
        }
      ]
    },
    {
      path: '/wap',
      name: 'wap',
      component: wapMain,
      children: [
        {
          path: '/',
          component: wapIndex
        },
        {
          path: ':id/details',
          component: wapDetails
        },
        {
          path: 'knowledge',
          component: wapKnowledge
        }
      ]
    },
    {
      path: '/admin',
      name: 'admin',
      component: adminMain,
      children: [
        {
          path: '/',
          component: adminIndex,
          children: [
            {
              path: '/',
              component: adminUser
            },
            {
              path: 'dream',
              component: adminDream
            }
          ]
        },
        {
          path: 'login',
          component: adminLogin
        }
      ]
    }
  ]
})



