<template>
  <div style="height:100%;text-align: center">
    <el-form :model="form" style="width:40%;margin:10% auto" label-width="100px">
      <el-form-item><img style="width:70px;height:70px" src="http://www.gqt.org.cn/ccylmaterial/tuanhui/200612/W020070123448949739156.gif"/></el-form-item>
      <el-form-item label="用户名">
        <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="form.password" type="password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">登 录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {login} from '@/interface/admin'
import cookie from 'js-cookie'
export default {
  data () {
    return {
      form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    async onSubmit () {
      let {data: {token, user}} = await login(this.form)
      cookie.set('token', token)
      cookie.set('id', user.id)
      this.$router.push({path: '/admin'})
    }
  }
}
</script>
<style>
.icon {
  width: 40%;
  margin-top: 20% auto
}
</style>




