<template>
  <el-container style="height: 100%; border: 1px solid #eee">
    <el-header style="text-align: right; font-size: 12px;padding-right:20px">
      <div style="float:left; padding-top:5px">
        <el-row>
          <el-col :span="5"><img style="width:50px;height:50px" src="http://www.gqt.org.cn/ccylmaterial/tuanhui/200612/W020070123448949739156.gif"></el-col>
          <el-col :span="18"><div class="header-title">宁波共青团·万人圆梦</div></el-col>
        </el-row>
      </div>
      <el-dropdown>
        <span class="el-dropdown-link" style="color:#ffffff">
          {{userInfo.username}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item><el-button type="text" @click="logout">退出</el-button></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246);height:100%">
    <el-menu>
      <router-link :to="{path: '/admin'}">
        <el-menu-item index="1">
          <i class="el-icon-document"></i>
          <span slot="title">用户管理</span>
        </el-menu-item>
      </router-link>
      <router-link :to="{path: '/admin/dream'}">
        <el-menu-item index="2">
          <i class="el-icon-goods"></i>
          <span slot="title">愿望管理</span>
        </el-menu-item>
      </router-link>
    </el-menu>
  </el-aside>
    <el-main>
      <router-view/>
    </el-main>
  </el-container>
</el-container>
</template>
<style>
  .el-header {
    background-color: #545c64;
    color: #f4f4f4;
    line-height: 60px;
  }
  .header-title{
    width: 400px;
    text-align: left;
    padding-left: 10px;
    height: 50px;
    line-height: 50px;
    font-size: 18px
  }
</style>

<script>
import {getUsers} from '@/interface/admin'
import Cookie from 'js-cookie'
export default {
  data () {
    return {
      userInfo: {}
    }
  },
  async mounted () {
    let {data} = await getUsers(Cookie.get('id'))
    this.userInfo = data
  },
  methods: {
    logout () {
      this.$axios.post('/api/v1/auth/token/refresh/', {token: Cookie.get('token')}).then(res => {
        Cookie.remove('token')
        Cookie.remove('id')
        this.$router.push('/admin/login')
      })
    }
  }
}
</script>


