<template>
  <div style="background-color:#fff">
    <el-button-group>
      <el-button icon="el-icon-plus" @click="addUser">新增</el-button>
    </el-button-group>
    <el-table
      ref="multipleTable"
      :data="list"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名">
      </el-table-column>
      <el-table-column
        prop="fullname"
        label="姓名">
      </el-table-column>
      <el-table-column
        prop="phone_number"
        label="手机号"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="last_login"
        label="最后登录时间"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :title="'新增用户'" :visible.sync="dialogFormVisible" width="40%">
      <el-form :model="group" ref="group" :rules="rules" :style="'width:70%;margin:0 auto'" label-width = "80px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="group.username"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="fullname">
          <el-input v-model="group.fullname"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone_number">
          <el-input v-model="group.phone_number"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="group.password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit('group')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {getUsers, deleteUser, postUsers} from '@/interface/admin'
export default {
  data () {
    let user = (rule, value, callback) => {
      if (!/^\w+$/.test(value)) {
        callback(new Error('用户名只能由大小写字母，数字和下划线组成'))
      } else {
        callback()
      }
    }
    let phone = (rule, value, callback) => {
      if (!/^1\d{10}$/.test(value)) {
        callback(new Error('请输入正确的手机号'))
      } else {
        callback()
      }
    }
    let passwd = (rule, value, callback) => {
      if (!/^[a-zA-Z\d!@#$%^&*()_+]{8,16}$/.test(value)) {
        callback(new Error('密码只能由大小写字母，数字和!@#$%^&*()_+组成，长度8到16位'))
      } else {
        callback()
      }
    }
    return {
      list: [],
      multipleSelection: [],
      dialogFormVisible: false,
      group: {
        username: '',
        fullname: '',
        phone_number: '',
        password: ''
      },
      rules: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {validator: user, trigger: 'change'}
        ],
        fullname: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        phone_number: [
          {required: true, message: '请输入手机号', trigger: 'blur'},
          {validator: phone, trigger: 'change'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {validator: passwd, trigger: 'change'}
        ]
      }
    }
  },
  mounted () {
    this.refresh()
  },
  methods: {
    async refresh () {
      let {data: {results}} = await getUsers()
      this.list = results
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    handleDelete (index, row) {
      this.$alert('删除', `删除${row.username}`, {
        showCancelButton: true,
        cancelButtonText: '取消',
        confirmButtonText: '确定',
        confirmButtonClass: 'danger'
      }).then(async () => {
        let {data} = await deleteUser(row.id)
        if (!data) {
          this.refresh()
          this.$notify({
            title: '成功',
            message: '删除成功',
            type: 'success'
          })
          return
        }
        this.$notify.error({
          title: '错误',
          message: '删除失败'
        })
      }).catch(() => {})
    },
    addUser () {
      this.dialogFormVisible = true
    },
    submit (ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          let {data} = postUsers(this.group)
          if (!data) {
            this.dialogFormVisible = false
            this.refresh()
            this.$notify({
              title: '成功',
              message: '新增用户成功',
              type: 'success'
            })
          }
        }
      })
    }
  }
}
</script>




