import axios from 'axios'
import querySrting from 'query-string'
axios.interceptors.response.use(response => {
  if (response === undefined) {
    return {response_error: true}
  }
  return response
}, err => {
  if (err && err.response.status) {
    console.log(err.response.status)
  }
})

export function dream ({type, form, page, pageSize, search, id}) {
  switch (type) {
    case 'get':
      return axios.get(`/api/v1/monitor/dream/${id || ''}?page=${page}&page_size=${pageSize}&${querySrting.stringify(search)}`)
  }
}
export function postCode (phone) {
  return axios.post(`/api/v1/monitor/send_code/`, {phone_num: phone})
}
export function submitClaim ({id, group}) {
  return axios.post(`/api/v1/monitor/dream/${id}/claim/`, group)
}



