<template>
  <div class="backgroud">
    <div class="self-form">
      <el-form :label-position="'right'" class="formBackgroud" label-width="1.8rem;">
        <el-row>
          <el-col :span="24">
            <el-form-item label="地区:">
              {{form.local}}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="姓名:">
              {{form.person_name}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="性别:">
              {{form.sex}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="微心愿:">
              {{form.title}}
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="基本情况:">
              {{form.reason}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="联系人：">
              {{form.contact_name }}
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="联系电话：">
              {{form.contact_phone }}
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div style="height:1.5rem"></div>
    <div class="footer">
      <yd-button size="large" :type="is_claimed?'disabled':'primary'" :disabled="is_claimed" @click.native="show=true">我要认领</yd-button>
    </div>
    <yd-popup v-model="show" position="bottom" height="40%">
      <yd-cell-group>
        <yd-cell-item>
          <span slot="left">姓名：</span>
          <yd-input slot="right" required :show-clear-icon="false" v-model="group.donor_name" placeholder="请输入您的姓名"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">手机号：</span>
          <yd-input slot="right" regex="mobile" v-model="group.phone_num" placeholder="请输入手机号码"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">验证码：</span>
          <yd-input slot="right" regex="^\d{4}$" v-model="group.code" placeholder="请输入验证码"></yd-input>
          <yd-sendcode
            slot="right"
            v-model="start"
            @click.native="sendCode"
            type="warning"
          ></yd-sendcode>
        </yd-cell-item>
        <yd-cell-item>
          <yd-checkbox slot="left" v-model="check">同意并接受</yd-checkbox>
          <span slot="right">《用户支持协议》</span>
        </yd-cell-item>
        <yd-button size="large" :type="check?'warning':'disabled'" @click.native="submit">认领提交</yd-button>
      </yd-cell-group>
    </yd-popup>
  </div>
</template>
<script>
import {dream, postCode, submitClaim} from '@/interface'
export default {
  data () {
    return {
      start: false,
      show: false,
      check: true,
      form: {},
      group: {
        donor_name: '',
        phone_num: '',
        code: ''
      },
      id: this.$router.history.current.params.id,
      is_claimed: true
    }
  },
  async mounted () {
    let {data} = await dream({type: 'get', id: this.id})
    this.form = data
    this.is_claimed = data.is_claimed
  },
  methods: {
    // 表单提交
    async submit () {
      this.$dialog.loading.open('正在提交中')
      submitClaim({id: this.id, group: this.group}).then(res => {
        if (res && !res.response_error) {
          setTimeout(() => {
            this.$dialog.loading.close()
            this.$dialog.alert({
              mes: '恭喜您认领成功啦，点击右上角分享到朋友圈号召更多爱心人士来认领吧！'
            })
            this.is_claimed = true
            this.show = false
            this.check = false
          }, 2000)
        } else {
          this.$dialog.loading.close()
          this.$dialog.alert({
            mes: '<p style="text-align: left">亲~~</p><p style="text-align: left">爱心人士真的好多啊，您选择的这个微心愿刚刚已被其他爱心人士认领啦~~请您继续选择其他微心愿进行认领哦，么么哒~~</p>'
          })
          this.show = false
          this.check = false
        }
      })
    },
    // 验证码发送
    async sendCode () {
      if (!/^1\d{10}$/.test(this.group.phone_num)) {
        this.$dialog.toast({
          mes: '手机号错误！',
          icon: 'error',
          timeout: 1500
        })
        return
      }
      this.$dialog.loading.open('发送中...')
      await postCode(this.group.phone_num)
      setTimeout(() => {
        this.start = true
        this.$dialog.loading.close()
        this.$dialog.toast({
          mes: '已发送',
          icon: 'success',
          timeout: 1500
        })
      }, 500)
    }
  }
}
</script>

<style scoped>
.self-form{
  position: relative;
  padding-top: 3rem;
  top: 1.2rem
}
.yd-alert .yd-confirm-bd{
  text-align: left;
}
.formBackgroud{
  background-color: rgba(255, 255, 255, 0.9);
  padding: 0.5rem;
  border-radius: 0.1rem
}
.backgroud{
  width: 100%;
  background-image: url('../../assets/wxbj.jpg');
  /* 背景图垂直、水平均居中 */
  background-position: center center;

  /* 背景图不平铺 */
  background-repeat: no-repeat;

  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;

  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
}
.backgroud .el-form-item__content,.backgroud .el-form-item__label{
  font-size: 0.25rem
}
.footer{
  position: fixed;
  height: 1.5rem;
  width: 100%;
  bottom: 0;
}
</style>



