                      <el-col>扫一扫关注宁波共青团让我们一起温暖全城</el-col>
                    </el-row>
                  </div>
                </el-card>
                <el-card :body-style="'padding:0px'" :style="'margin-top:10px'" :class="'details-card'">
                  <img src="http://element.eleme.io/static/hamburger.50e4091.png" class="image">
