                </el-card>
              </div>
              <div style="height:40px;width:150px;line-height:40px;border-bottom: 1px solid #8f8f8f">注意事项</div>
              <div style="padding-top:20px;border-top: 1px solid #8f8f8f"></div>
            </el-col> -->
          <!-- </el-row> -->
          <el-row>
            <el-col>
              <div style="text-align: center">
                <el-button type="success" @click="donation" :disabled="is_claimed">我要捐赠</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" width="30%">
      <el-form :model="group" ref="group" :rules="rules" :style="'margin:0 auto'" label-width = "80px">
        <el-form-item label="姓名" prop="donor_name">
          <el-input v-model="group.donor_name"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone_num">
          <el-input v-model="group.phone_num"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="code">
          <el-input v-model="group.code">
            <el-button slot="append" @click="sendCode" :disabled="!sendCodeBut"><yd-countdown v-if="!sendCodeBut" :time="60" timetype="second" :callback="countdown"><span>{%s}秒</span></yd-countdown><span v-show="sendCodeBut">获取验证码</span></el-button>
          </el-input>
        </el-form-item>
        <el-form-item prop="checked">
          <el-checkbox v-model="group.checked">同意并接受</el-checkbox>&nbsp;&nbsp;&nbsp;<span>《用户支持协议》</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" :disabled="isSubmit" @click="submit('group')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {dream, postCode, submitClaim} from '@/interface'
export default {
  data () {
    let phone = (rule, value, callback) => {
      if (!/^1\d{10}$/.test(value)) {
        callback(new Error('请输入正确的手机号'))
      } else {
        callback()
      }
    }
    return {
      dialogTitle: '认领项目-不爱迪赛的',
      formLabelWidth: '80px',
      dialogFormVisible: false,
      form: {},
      group: {
        donor_name: '',
        phone_num: '',
        code: '',
        checked: true
      },
      rules: {
        donor_name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        phone_num: [
          {required: true, message: '请输入手机号', trigger: 'blur'},
          {validator: phone, message: '手机号格式错误', trigger: 'change'}
        ],
        code: [
          {required: true, message: '请输入验证码', trigger: 'blur'}
        ],
        checked: [
          {required: true, message: '同意协议', trigger: 'change'}
        ]
      },
      sendCodeBut: true,
      isSubmit: false,
      id: this.$router.history.current.params.id,
      is_claimed: false
    }
  },
  async mounted () {
    let {data} = await dream({type: 'get', id: this.id})
    this.form = data
    this.dialogTitle = data.title
    this.is_claimed = data.is_claimed
  },
  methods: {
    // 表单提交
    async submit (ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          submitClaim({id: this.id, group: this.group}).then(res => {
            if (!res.response_error) {
              this.dialogFormVisible = false
              this.is_claimed = true
              this.$notify({
                title: '成功',
                message: '认领成功',
                type: 'success'
              })
              return true
            }
            this.$notify.error({
              title: '捐赠失败',
              message: '爱心人士真的好多啊，这个微心愿已被其他人认领啦!'
            })
          }).catch(err => {
            console.log(err)
          })
        }
      })
    },
    donation () {
      this.dialogFormVisible = true
    },
    countdown () {
      this.sendCodeBut = true
    },
    async sendCode () {
      if (/^1\d{10}$/.test(this.group.phone_num)) {
        await postCode(this.group.phone_num)
        this.sendCodeBut = false
      }
    }
  }
}
</script>
<style>
.details-main{
  background: url(../../assets/bj.jpg)
}
.content{
  margin-top: 10px;
}
.details-content{
  padding-top: 100px;
}
.details{
  min-height: 700px;
  width: 100%;
}
.details .label{
  width: 100%;
  height: 30px;
  padding: 10px 0;
  background-color: red;
  text-align-last: center;
  border-radius: 20px;
  font-size: 18px;
  line-height: 25px;
  color: #ffffff
}
.details .label-for{
  line-height: 50px;
  font-size: 18px;
  font-weight: 500
}
.details-content .el-form .el-form-item__label,.details-content .el-form .el-form-item__content{
  font-size: 16px;
  font-weight: 500
}
.details-card .el-card__body{
  display: flex
}
.details-card .el-card__body img{
  height: 100px;
}
</style>




