<template>
  <div>
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="14" :offset="5">
            <el-row>
              <el-col :span="1"><img class="tuanhui" src="http://www.gqt.org.cn/ccylmaterial/tuanhui/200612/W020070123448949739156.gif"></el-col>
              <el-col :span="20"><div class="header-title">宁波市青少年万人圆梦行动</div></el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <div style="background-color: #ffffff">
          <router-view/>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'web',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>
<style>
  .el-main{
    padding: 0;
  }
  .tuanhui{
    width: 50px;
  }
  .el-header {
    padding: 0
  }
  .header-title{
    padding-left: 10px;
    height: 50px;
    line-height: 50px;
    font-size: 15px
  }
</style>



