import axios from 'axios'
import Cookie from 'js-cookie'
import querySrting from 'query-string'
import router from '@/router'
axios.interceptors.request.use(config => {
  let token = Cookie.get('token')
  if (token) {
    config.headers['Authorization'] = `jwt ${token}`
  }
  return config
}, err => {
  console.log(err)
})
axios.interceptors.response.use(response => {
  return response
}, err => {
  if (err && err.response.status) {
    switch (err.response.status) {
      case 401 :
        router.push({path: '/admin/login'})
        break
    }
  }
})

export function login (form) {
  return axios.post('/api/v1/auth/login/', form)
}

export function getUsers (id) {
  return axios.get(`/api/v1/auth/users/${id || ''}`)
}

export function deleteUser (id) {
  return axios.delete(`/api/v1/auth/users/${id}`)
}

export function postUsers (form) {
  Object.assign(form, {is_superuser: false, is_staff: true, is_admin: false})
  return axios.post(`/api/v1/auth/users/`, form)
}

export function dream ({type, form, page, pageSize, ids, search}) {
  switch (type) {
    case 'get':
      return axios.get(`/api/v1/monitor/dream/${ids || ''}?page=${page}&page_size=${pageSize}&${querySrting.stringify(search)}`)
    case 'delete':
      return axios.delete(`/api/v1/monitor/dream/${ids || ''}`)
    case 'patch':
      return axios.patch(`/api/v1/monitor/dream/${ids || ''}/`, form)
    case 'post':
      return axios.post(`/api/v1/monitor/dream/${ids || ''}`, form)
  }
}



