<template>
  <div class="item">
    <el-card :body-style="{ padding: '5px' }" :class="'item-card'">
      <div class="img">
        <img :src="data.image_url" class="image">
      </div>
      <div style="padding: 14px;" class="content">
        <el-row :gutter="20">
          <el-col :span="18" style="font-size:15px;font-weight: 500">{{data.title}}</el-col>
          <el-col :span="6">项目状态：{{data.is_claimed ? '已认领' : '未认领'}}</el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="18">项目简介：{{data.want}}</el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="18">发布时间:{{data.updated_at}}</el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="18">发布方：{{data.contact_person}}</el-col>
          <el-col :span="6"><el-button type="success" plain size="small" :disabled="data.is_claimed" @click="click">我要认领</el-button></el-col>
        </el-row>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  props: ['data', 'receive', 'noReceive'],
  methods: {
    click () {
      this.$emit('claim', this.data.id)
    }
  }
}
</script>
<style>
.item-card .el-card__body{
  display: flex;
}
.item-card .el-card__body img{
  height: 150px;
}
.item-card .el-card__body .content{
  flex: 1;
}
</style>



