<template>
  <div>
    <!-- <el-carousel height="650px">
      <el-carousel-item v-for="(item, index) in caArr" :key="index">
        <img :src="item">
      </el-carousel-item>
    </el-carousel> -->
    <div class="main">
      <el-row>
        <el-col :span="14" :offset="5">
          <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
            <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
          </el-breadcrumb>
          <div class="search">
            <el-row>
              <el-col>
                <el-form :inline="true" class="search-form">
                  <el-row :gutter="10">
                    <el-col :span="17">
                      <el-form-item label="项目状态">
                        <el-select v-model="searchValue.is_claimed" @change="getDate(1)" clearable placeholder="请选择">
                          <el-option label="已认领" value="true"></el-option>
                          <el-option label="未认领" value="false"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="地区">
                        <el-select v-model="searchValue.local__icontains" clearable placeholder="请选择">
                          <el-option
                            v-for="(item, index) in loacls"
                            :key="index"
                            :value="item">
                          </el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label-width="80px" label=" ">
                        <el-button type="text" icon="el-icon-info" @click="viewRules">认领须知</el-button>
                      </el-form-item>
                    </el-col>
                    <el-col :span="7" class="text-right">
                      <el-form-item>
                        <el-input placeholder="请输入内容" v-model="searchValue.title__icontains" class="input-with-select">
                          <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
                        </el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
              </el-col>
            </el-row>
          </div>
          <yd-layout>
            <yd-infinitescroll :distance="300" :callback="loadList" ref="dreamList">
              <div class="list" slot="list">
                <item v-for="(item, index) in list" :key="index" :data="item" @claim="claim" :receive="receive" :noReceive="noReceive"/>
              </div>
              <span slot="doneTip">没有数据啦~~</span>
            </yd-infinitescroll>
          </yd-layout>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import Item from '@/view/web/components/item'
import {dream} from '@/interface'
import ca1 from '@/assets/carousel1.jpg'
import ca2 from '@/assets/carousel2.jpg'
import receive from '@/assets/receive.png'
import noReceive from '@/assets/noReceive.png'
export default {
  data () {
    return {
      loacls: ['海曙','江北','镇海','北仑','鄞州','奉化','余姚','慈溪','宁海','象山','高新','其他'],
      receive,
      noReceive,
      page: 1,
      page_size: 10,
      caArr: [ca1, ca2],
      searchValue: {
        is_claimed: '',
        local__icontains: '',
        title__icontains: ''
      },
      projectStatus: [
        {
          key: 111,
          value: '才能杀可能'
        }
      ],
      list: []
    }
  },
  mounted () {
    this.$axios.get(`/api/v1/monitor/dream/?page=${this.page}&page_size=${this.page_size}`).then(res => {
      this.list.push(...res.data.results)
    })
  },
  methods: {
    search () {
      this.page = 1
      this.getDate()
    },
    viewRules () {
      this.$alert(`
      <p>1.请您填写正确的联系方式。微心愿认领人在了解微心愿基本情况之后，请点击<strong>“我要认领”</strong>选项栏填写您的联系方式，以确定认领意愿，已被认领的微心愿将自动变灰。如果多人同时认领同一个微心愿，系统将自动选择第一个提交联系方式的微心愿认领人，其他未被选中的微心愿认领人可以再选择其他的微心愿进行认领。</p>
      <p>2.我们会尽快与您对接微心愿。微心愿认领成功后，将有短消息分别发送至微心愿认领人和微心愿联系人（一般为青少年所在的乡镇街道团干部或所在学校的老师）的手机上，请确保不屏蔽相关短信。微心愿联系人和微心愿认领人将尽快通过电话进行联系沟通，商定微心愿的圆梦方式和圆梦时间，并由微心愿联系人协助微心愿认领人将关爱及时送达需要帮助的青少年手中。</p>
      <p>3.微心愿认领人可在微信朋友圈发起分享<strong>“我已经参与2018年宁波市青少年万人圆梦行动微心愿认领行动”</strong>，动员更多的爱心人士加入到行动中来。</p>`, '捐赠规则', {
        dangerouslyUseHTMLString: true
      })
    },
    async loadList () {
      let results = await this.getDate()
      if (results.length < this.page_size) {
        this.$refs.dreamList.$emit('ydui.infinitescroll.loadedDone')
        return
      }
      this.$refs.dreamList.$emit('ydui.infinitescroll.finishLoad')
    },
    claim (id) {
      this.$router.push({path: `/index/${id}/details`, query: {id: id}})
    },
    async getDate (page) {
      let {data: {results}} = await dream({type: 'get', page: page || this.page, pageSize: this.page_size, search: this.searchValue})
      if (this.page === 1) {
        this.list = results
      } else {
        this.list.push(...results)
      }
      this.page++
      return results
    }
  },
  components: {
    Item
  }
}
</script>

<style>
.yd-flexview{
  max-height: 1000px;
  max-width: 100%;
}
.main{
  padding-top: 20px
}
.breadcrumb{
  padding-bottom: 10px
}
.search-form{
  background: #f8f8f8;
  padding: 5px
}
.text-right{
  text-align: right
}
.el-form--inline .el-form-item{
  margin: 0
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  text-align: center
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}
</style>



