<template>
  <div class="wap">
    <yd-slider autoplay="3000">
        <yd-slider-item v-for="(item, index) in caArr" :key="index">
            <img :src="item">
        </yd-slider-item>
    </yd-slider>
    <yd-radio-group slot="left" v-model="searchValue.is_claimed" style="height:1rem;padding-left:0.5rem; display: table">
      <yd-radio val="" style="display: table-cell;vertical-align: middle;">全部</yd-radio>
      <yd-radio val="true" style="display: table-cell;vertical-align: middle;">已认领</yd-radio>
      <yd-radio val="false" style="display: table-cell;vertical-align: middle;">未认领</yd-radio>
    </yd-radio-group>
    <yd-cell-group style="margin-bottom: 0">
      <yd-cell-item arrow type="link" href="/wap/knowledge">
        <span slot="left">认领须知</span>
      </yd-cell-item>
      <yd-cell-item arrow>
        <span slot="left">地区：</span>
        <select slot="right" v-model="searchValue.local__icontains">
          <option :value="''">全部</option>
          <option v-for="(item, index) in loacls" :value="item" :key="index">{{item}}</option>
        </select>
      </yd-cell-item>
    </yd-cell-group>
    <yd-flexbox>
      <yd-flexbox-item><yd-search v-model="search1" :on-submit="search"></yd-search></yd-flexbox-item>
    </yd-flexbox>
    <yd-layout>
      <yd-infinitescroll :callback="loadList" ref="dreamList">
        <yd-list theme="4" slot="list">
          <yd-list-item v-for="(item, key) in list" :key="key" :href="`#/wap/${item.id}/details?id: ${item.id}`">
              <img slot="img" :src="item.image_url">
              <span slot="title">{{item.title}}</span>
              <yd-list-other slot="other">
                {{item.reason}}
              </yd-list-other>
          </yd-list-item>
        </yd-list>
        <!-- 数据全部加载完毕显示 -->
        <span slot="doneTip">没有数据啦~~</span>
      </yd-infinitescroll>
    </yd-layout>
  </div>
</template>
<script>
import {dream} from '@/interface'
import ca1 from '@/assets/carousel1.jpg'
import receive from '@/assets/receive.png'
import noReceive from '@/assets/noReceive.png'
export default {
  data () {
    return {
      loacls: ['海曙','江北','镇海','北仑','鄞州','奉化','余姚','慈溪','宁海','象山','高新','其他'],
      receive,
      noReceive,
      caArr: [ca1],
      searchValue: {
        is_claimed: '',
        title__icontains: ''
      },
      search1: '',
      page: 1,
      page_size: 10,
      list: []
    }
  },
  async mounted () {
    this.getDate()
  },
  watch: {
    searchValue: {
      handler: function (val, oldVal) {
        this.page = 1
        this.getDate()
      },
      deep: true
    }
  },
  methods: {
    search () {
      this.searchValue.title__icontains = this.search1
      setTimeout(() => {
        this.search1 = this.searchValue.title__icontains
      }, 500)
      this.page = 1
      this.getDate()
    },
    async loadList () {
      let results = await this.getDate()
      if (results.length < this.page_size) {
        this.$refs.dreamList.$emit('ydui.infinitescroll.loadedDone')
        return
      }
      this.$refs.dreamList.$emit('ydui.infinitescroll.finishLoad')
    },
    async getDate () {
      let {data: {results}} = await dream({type: 'get', page: this.page, pageSize: this.page_size, search: this.searchValue})
      if (this.page === 1) {
        this.list = results
      } else {
        this.list.push(...results)
      }
      this.page++
      return results
    }
  }
}
</script>

<style>
.wap .yd-flexview{
  height: 8rem;
}
</style>



